.postAuthor {
  display: flex;
  justify-content: space-between;
  column-gap: 10px;
  align-items: center;
  cursor: pointer;
}

.authorName {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #2b2c34;
}

.authorPostDate {
  /* font-family: "Poppins"; */
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: capitalize;
  color: #718797;
}

.authorImg {
  display: flex;
  flex: 1 0 auto;
}

.authorImg__img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
